import { useEffect, useState } from 'react'
import { getUserRemainingDelay } from 'utils/callHelpers'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { useMasterYield } from './useContract'
import useRefresh from './useRefresh'

const useGetUserRemainingDelay = () => {
  const [val, setVal] = useState(0)
  const { account }: { account: string } = useWallet()
  const { fastRefresh } = useRefresh()
  const contract = useMasterYield()
  useEffect(() => {
    if (account && !val) getUserRemainingDelay(contract, account).then(setVal)
  }, [account, val, contract, fastRefresh])
  return val
}

export default useGetUserRemainingDelay
