import React from 'react'
import styled from 'styled-components'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { Heading, BaseLayout, Card, CardBody, Button, Text, Flex } from 'uikit'
import Page from 'components/layout/Page'
import BalanceInput from 'components/Input/BalanceInput'
import { useContractApprove } from 'hooks/useApprove'
import BigNumber from 'bignumber.js'
import { useERC20, useMasterYield } from 'hooks/useContract'
import Countdown, { CountdownRenderProps } from 'react-countdown'
import UnlockButton from 'components/UnlockButton'
import { swapTokens, swapBackTokens } from 'utils/callHelpers'
import { getAllowance } from 'utils/erc20'
import { getYieldContract, getYieldTokenAddress } from 'utils/addressHelpers'
import useTokenBalance from 'hooks/useTokenBalance'
import { getBalanceNumber } from 'utils/formatBalance'
import useGetUserRemainingDelay from 'hooks/useGetUserRemainingDelay'

const Cards = styled(BaseLayout)`
  align-items: stretch;
  justify-content: stretch;
  margin-bottom: 48px;

  & > div {
    grid-column: span 6;
    width: 100%;
  }

  ${({ theme }) => theme.mediaQueries.sm} {
    & > div {
      grid-column: span 8;
    }
  }

  ${({ theme }) => theme.mediaQueries.lg} {
    & > div {
      grid-column: span 6;
    }
  }
`

const StyledCard = styled(Card)`
  min-height: 376px;
`

const SwapCard: React.FC<any> = ({ symbol, account, title, tokenContract, isBack }) => {
  const balance = useTokenBalance(tokenContract)
  const [isApproved, setIsApproved] = React.useState(false)
  const [value, setValue] = React.useState('')
  const onChange = (e) => setValue(e.target.value)
  const onSelectMax = () => setValue(balance.toString())
  const [requestedApproval, setRequestedApproval] = React.useState(false)
  const contract = useMasterYield()
  const contractLP = useERC20(tokenContract)
  const { onApprove } = useContractApprove(contractLP, contract)
    const pendingLock = useGetUserRemainingDelay()
    
  React.useEffect(() => {
    getAllowance(contractLP, contract, account).then((x) => {
      if (!account) return
      const allowance = new BigNumber(x)
      setIsApproved(allowance && allowance.isGreaterThan(0))
    })
  }, [account, contract, contractLP])

  const handleApprove = React.useCallback(async () => {
    try {
      setRequestedApproval(true)
      await onApprove()
      setRequestedApproval(false)
    } catch (e) {
      console.error(e)
    }
  }, [onApprove])

  const handleSwap = React.useCallback(async () => {
    try {
      setRequestedApproval(true)
      if (isBack) await swapBackTokens(contract, value, account)
      else await swapTokens(contract, value, account)
      setRequestedApproval(false)
    } catch (e) {
      console.error(e)
    }
  }, [contract, value, account, isBack])

  const renderer = (countdownProps: CountdownRenderProps) => {
    const { hours, minutes, seconds } = countdownProps
    const paddedSeconds = seconds < 10 ? `0${seconds}` : seconds
    const paddedMinutes = minutes < 10 ? `0${minutes}` : minutes
    const paddedHours = hours < 10 ? `0${hours}` : hours

    return (
      <Flex>
        <Text bold textTransform="uppercase" color="secondary" fontSize="18px" pr="3px">
          Lock ends
        </Text>
        <Text bold textTransform="uppercase" color="textSubtle" fontSize="18px">
          {paddedHours}:{paddedMinutes}:{paddedSeconds}
        </Text>
      </Flex>
    )
  }

  return (
    <StyledCard>
      <CardBody>
        <Heading size="md" mb="20px">
          {title}
        </Heading>
        <BalanceInput
          max={getBalanceNumber(balance)}
          symbol={symbol}
          onChange={onChange}
          onSelectMax={onSelectMax}
          value={value}
        />
        {isApproved ? (
          <Button mt="8px" fullWidth disabled={!value || requestedApproval} onClick={handleSwap}>
            {title}
          </Button>
        ) : (
          <Button mt="8px" fullWidth disabled={!value || requestedApproval} onClick={handleApprove}>
            Approve Contract
          </Button>
        )}
        {isBack && pendingLock && <Countdown date={new Date(new Date().getTime() + (pendingLock *1000))} renderer={renderer}/>}
      </CardBody>
    </StyledCard>
  )
}

const Yield: React.FC = () => {
  const { account }: { account: string } = useWallet()
  const lpToken = getYieldContract()
  const token = getYieldTokenAddress()

  return (
    <Page>
      <Heading size="xl" mb="32px">
        Passive Yield Token (A010)
      </Heading>
      <Cards>
        {account && (
          <>
            <SwapCard symbol="C101-A009" account={account} title="Swap" tokenContract={lpToken} isBack={false} />
            <SwapCard symbol="A010" account={account} title="Swap Back" tokenContract={token} isBack />
          </>
        )}
        <StyledCard>
          <CardBody>
            <Heading size="md" mb="20px">
              How to Swap
            </Heading>
            <p>You need to be an active Agent.</p>
            <br />
            <p>
              You can swap any amount of <b>C101-A009 LP</b>, and this deposit will be locked for 7 days.
            </p>
            <br />
            <p>
              If you deposit before 7 days, the timer will <b>reset</b> to 7 days again.
            </p>
            <br />
            <p>
              Deposit will <b>burn</b> 2% of the LP.
            </p>
            <br />
            <p>You need to pay back the same amount to release your LP less the fee.</p>
            <br />
            <p>A010 is a passive yield token, you will get more for just holding it in your wallet.</p>
            <br />
            {!account && <UnlockButton fullWidth />}
          </CardBody>
        </StyledCard>
      </Cards>
    </Page>
  )
}

export default Yield
